@charset "UTF-8";
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600,300);
/* Base Styles */

#cssmenu,
#cssmenu ul,
#cssmenu li,
#cssmenu a {
    margin: 0;
    padding: 0;
    border: 0;
    list-style: none;
    font-weight: normal;
    text-decoration: none;
    line-height: 1;
    font-family: 'Open Sans', sans-serif;
    font-size: 0.80rem;
    position: relative;
}
#cssmenu a {
    line-height: 1.5;
}
#cssmenu {
    width: auto;
    background: $theme-color3;
}
#cssmenu > ul {
    border: 1px solid #000;
    -moz-box-shadow: inset 1px 1px 0 rgba(255, 255, 255, 0.2);
    -webkit-box-shadow: inset 1px 1px 0 rgba(255, 255, 255, 0.2);
    box-shadow: inset 1px 1px 0 rgba(255, 255, 255, 0.2);
    background: $theme-color3;
 //   background: -moz-linear-gradient(#2a5c89 0%, #193752 100%);
   // background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #2a5c89), color-stop(100%, #193752));
 //   background: -webkit-linear-gradient(#2a5c89 0%, #193752 100%);
  //  background: linear-gradient(#2a5c89 0%, #193752 100%);
 //   -webkit-border-radius: 4px;
  //  -moz-border-radius: 4px;
  //  border-radius: 4px;
}
#cssmenu > ul:after {
    clear: both;
    content: '';
    display: table;
}
@media all and (max-width: 480px) {
    #cssmenu > ul {
        max-height: 32px;
        overflow: hidden;
        -webkit-transition: max-height 0.5s;
        transition: max-height 0.5s;
    }
    #cssmenu.expand ul {
        max-height: 900px;
    }
}
#cssmenu > ul > li {
    // border-right: 1px solid #000;
   // -moz-box-shadow: 1px 0 0 rgba(255, 255, 255, 0.2);
    //-webkit-box-shadow: 1px 0 0 rgba(255, 255, 255, 0.2);
    //box-shadow: 1px 0 0 rgba(255, 255, 255, 0.2);
    float: left;

}
#cssmenu > ul > li.mobile {
    display: none;
}
#cssmenu > ul > li.active {
    background: $theme-color3;
    /*
    background: -moz-linear-gradient(#224a6e 0%, #193752 100%);
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #224a6e), color-stop(100%, #193752));
    background: -webkit-linear-gradient(#224a6e 0%, #193752 100%);
    background: linear-gradient(#224a6e 0%, #193752 100%);*/
}
@media all and (max-width: 480px) {
    #cssmenu > ul > li {
        border-right: 0 none;
        -moz-box-shadow: 0 0 0 transparent;
        -webkit-box-shadow: 0 0 0 transparent;
        box-shadow: 0 0 0 transparent;
        display: block;
        float: none;
    }
    #cssmenu > ul > li.mobile {
        display: block;
    }
    #cssmenu > ul > li.mobile i {
        float: right;
        font-style: normal;
    }
}
#cssmenu > ul > li:last-child {
    border-right: 0 none;
    -moz-box-shadow: 0 0 0 transparent;
    -webkit-box-shadow: 0 0 0 transparent;
    box-shadow: 0 0 0 transparent;
    -webkit-border-radius: 0 4px 4px 0;
    -moz-border-radius: 0 4px 4px 0;
    border-radius: 0 4px 4px 0;
}
#cssmenu > ul > li:last-child ul {
    left: auto;
    right: 0;
}
#cssmenu > ul > li.mobile {
    border-right: 0 none;
    -moz-box-shadow: 0 0 0 transparent;
    -webkit-box-shadow: 0 0 0 transparent;
    box-shadow: 0 0 0 transparent;
}
#cssmenu > ul > li > a {
    margin: 0 0 2px 0;
    font-size: 0.85em;
    display: block;
    color: #fff;
    text-shadow: 0 1px 1px #000;
}
#cssmenu > ul > li > a > span {
    display: block;
    padding: 6px 10px;
    font-weight: bold;
}
#cssmenu > ul > li > a:hover {
    text-decoration: none;
}
#cssmenu > ul > li.active {
    border-bottom: none;
}
#cssmenu > ul > li.active > a {
    color: #fff;
    text-shadow: 0 1px 1px #000;
}
/* Sub menu */
#cssmenu ul ul {
    background: #F9F9F9;
    border: 1px solid #CCC;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;
    -moz-box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.25);
    -webkit-box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.25);
    box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.25);
    padding: 5px 12px;
    display: none;
    position: absolute;
    top: 33px;
    left: 0;
    width: 200px;
    z-index: 1;
}
@media all and (max-width: 480px) {
    #cssmenu ul ul {
        position: relative;
        top: 0;
        width: 100%;
    }
}
#cssmenu ul ul li {
    padding: 3px 0;
}
#cssmenu ul ul a {
    color: #999;
    display: block;
    font-size: 0.90em;
    font-weight: bold;
}
#cssmenu ul ul a:hover {
    color: #444;
}